@import '../helpers/var';

.reviews {
    width: 100%;
    background: $gray url('../img/reviews-bg.jpg') center / cover no-repeat;

    &__inner {
        padding: 7.5% 0;
        align-items: center;
        background-color: rgba($white, 0.35); }

    &__title {
        text-align: center; }

    &__subtitle {
        width: 55%;
        text-align: center;
        color: $white; } }

.reviews-slider {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 5rem auto;
    margin: 0 auto;
    max-width: 100%;

    &--wrapper {
        // min-height: 35vh
        overflow: hidden;
        position: relative; }

    &__inner {
        height: 100%;
        position: relative;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center; } }

.review-description {
    display: none;
    height: 100%;
    position: absolute;
    width: 100%;

    // &:first-child
    //     display: block

    // &:nth-of-type(1)
    //     background: #D7A151

    // &:nth-of-type(2)
    //     background: #F4E4CD

    // &:nth-of-type(3)
    //     background: #C75534

    // &:nth-of-type(4)
    //     background: #D1D1D4

    &_buttons {
        left: 0;
        position: absolute;
        right: 0;
        text-align: center; }

    .slide {
        &_btn {
            color: #474544;
            font-size: 42px;
            margin: 0 0.175em;
            -webkit-transition: all 0.4s ease-in-out;
            -moz-transition: all 0.4s ease-in-out;
            -ms-transition: all 0.4s ease-in-out;
            -o-transition: all 0.4s ease-in-out;
            transition: all 0.4s ease-in-out; }

        &_btn.active, .slide_btn:hover {
            color: #428CC6;
            cursor: pointer; } } }

.directional_nav {
    height: 340px;
    margin: 0 auto;
    max-width: 940px;
    position: relative;
    top: -340px; }

.previous_btn {
    bottom: 0;
    left: 100px;
    margin: auto;
    position: absolute;
    top: 0; }

.next_btn {
    bottom: 0;
    margin: auto;
    position: absolute;
    right: 100px;
    top: 0; }

.previous_btn, .next_btn {
    cursor: pointer;
    height: 65px;
    opacity: 0.5;
    transition: opacity 0.4s ease-in-out;
    width: 65px; }

.previous_btn:hover, .next_btn:hover {
    opacity: 1; }

.review-description {
    display: flex;
    flex-direction: column;
    width: 75%;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    &__legend {
        display: flex;
        font-size: calc(1rem + 0.25vw);
        line-height: calc(1.25rem + 0.5vw);
        text-align: center;
        align-items: center;
        justify-content: center; }

    &__autor {
        display: flex;
        font-size: calc(1.25rem + 0.25vw);
        line-height: calc(1.25rem + 0.5vw);
        text-align: center;
        align-items: center;
        font-weight: 500;
        justify-content: center;
        margin: 2.5rem auto; } }
