}

.contact-us__form {
  width: 50%;
  margin: 0 auto;
  padding: 2.5% 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.5rem;
}

.contact-us__title {
  grid-column: 1/3;
  color: #ffffff;
}

.contact-us__description {
  grid-column: 1/3;
  font-size: calc(1.1rem + 0.25vw);
  line-height: calc(1.1rem + 0.75vw);
  width: 100%;
  color: #ffffff;
  letter-spacing: 0.005em;
}

.contact-us__input {
  width: 100%;
  margin: 2.5rem 0;
  padding: 1rem 0;
  background-color: #FFB800;
  border: none;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  font-size: calc(1rem + 0.25vw);
  line-height: calc(1rem + 0.25vw);
  color: #ffffff;
  letter-spacing: 0.07em;
  font-weight: 500;
}

.contact-us__textarea {
  grid-column: 1/3;
  font-size: calc(1rem + 0.25vw);
  line-height: calc(1rem + 0.25vw);
  width: 100%;
  padding: 1rem 0;
  background-color: #FFB800;
  border: none;
  border-bottom: 2px solid rgba(255, 255, 255, 0.5);
  resize: none;
  color: #ffffff;
  letter-spacing: 0.07em;
  font-weight: 500;
}

.footer {
  width: 100%;
  padding: 5% 0;
  display: flex;
  align-items: center;
  position: relative;
  background-color: #000000;
}

.footer__inner {
  height: 100%;
  justify-content: center;
  padding: 2.5% 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr) 5%;
  grid-gap: 5rem;
}

.footer__item {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.footer__description {
  font-size: calc(1rem + 0.25vw);
  line-height: calc(1rem + 0.75vw);
  margin: 2.5rem 0;
  font-weight: 300;
}

.footer__social-menu {
  flex-direction: column-reverse;
}

.footer__social-menu .social-menu__item {
  margin: 1rem auto;
}

.footer__social-menu .social-menu__link > svg {
  fill: #7C7C7C;
}

.footer__social-menu .social-menu__link > svg:hover {
  fill: #ffffff;
}

.footer-navigation__item {
  display: flex;
  justify-content: flex-end;
}

.footer-navigation__link {
  color: #ffffff;
  display: flex;
  width: 50%;
  text-align: left;
  padding: 1rem 0;
  font-size: calc(1rem + 0.25vw);
  line-height: calc(1rem + 0.75vw);
}

.footer-navigation__link:hover {
  color: #FFB800;
}

.contact {
  align-items: center;
}

.contact__item {
  width: 50%;
  justify-content: center;
  text-align: left;
  color: #ffffff;
  margin: 1rem 0;
  font-size: calc(1rem + 0.25vw);
  line-height: calc(1rem + 0.75vw);
  font-weight: 300;
}

.contact__item[href]:hover {
  color: #FFB800;
}

@media only screen and (max-width: 1366px) {
  .footer__inner {
    grid-template-columns: 35% repeat(2, 1fr) 5%;
  }
  .footer-navigation__link {
    width: 100%;
  }
  .contact__item {
    width: 100%;
  }
}

